.verticalTable{
    @media (max-width: 767px) {
        border-top: 1px solid var(--gray);
        border-bottom: 1px solid var(--gray);

        th, td {
            display: block;
            padding: 0;
            width: 100%;
            text-align: left;
        }

        tr {
            display: block;
            margin-bottom: 28px;
            border: 0;
            &:last-child{
                border: 0;
            }
        }

        th {
            margin-bottom: 5px;
            background-color: transparent;
            font-weight: bold;
        }

        td {
            margin-top: -5px;
        }

        .addressHeader{
            padding: 15px 10px;
            font-size: 12px;
            font-weight: 700;
            text-align: left;
            vertical-align: top;
            background-color: #FFFCF4;
        }

        .tableRow.flex {
            display: flex;
            align-items: flex-start;
            margin-bottom: 10px;
            padding: 0 12px;
            &:last-child{
                margin-bottom: 10px;
            }
        }
        
        .tableRow.flex th,
        .tableRow.flex td {
            margin: 0;
            width: auto;
            text-align: left;
            flex: 1;
        }
        
        .tableRow.flex th {
            flex-basis: auto;
            flex-grow: 0;
            flex-shrink: 1;
            margin-right: 10px;
        }
        
        .tableRow.flex td {
            flex-basis: auto;
            flex-grow: 1;
            flex-shrink: 1;
        }
        
    }
}

